import { Link, Route, Routes, Navigate } from "react-router-dom";
import { useState, useEffect } from 'react'
import Administration from "./pages/Adminstration";
import View from "./pages/View";
import Home from './pages/Home';
import Formations from "./pages/Formations";
import Temoignages from "./pages/temoignages";
import Contact from "./pages/Contact";
import Compte from "./pages/Compte";
import Candidature from "./pages/Candidature";
import Formation from "./pages/Formation";
import candidatureService from './services/candidature';


const App = () => {

  const [admin, setAdmin] = useState(false)
  const [options, setOptions] = useState(false)
  const [user, setUser] = useState(null)

  useEffect(() => {
    setAdmin(window.location.pathname === '/admin')
    const loggedUserJSON = window.localStorage.getItem('loggedInhtUser')
    if (loggedUserJSON !== null || undefined) {
      const user = JSON.parse(loggedUserJSON)
      candidatureService.setToken(user.token)
      setUser(user.userWithoutPass)
    }
  }, [])

  return (
    <div>
      <div className="col w-full fixed z-10">
        {
          user === null ?
            <></> :
            <div className={` ${admin ? 'hidden' : 'row'} relative w-full row justify-end items-center pr-[7.03125vw] bg-white border-b-2 border-newBlack h-[2.5vw]`}>
              <div className="relative h-full grid items-center" onMouseLeave={() => setOptions(false)}>
                <div className="row items-center cursor-pointer gap-[0.625vw]" onMouseEnter={() => setOptions(true)}>
                  <svg className="w-[2vw]" viewBox="0 0 31 31" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M15.5 0.5C11.5231 0.504528 7.71046 2.08633 4.8984 4.89839C2.08633 7.71045 0.504528 11.5231 0.5 15.5C0.5 19.5077 2.06116 23.2737 4.89478 26.1052C6.28392 27.5028 7.93657 28.6109 9.757 29.3653C11.5774 30.1197 13.5295 30.5053 15.5 30.4999C19.4769 30.4954 23.2895 28.9136 26.1016 26.1016C28.9137 23.2895 30.4955 19.4768 30.5 15.5C30.4955 11.5231 28.9137 7.71045 26.1016 4.89839C23.2895 2.08633 19.4769 0.504528 15.5 0.5ZM15.5 2.63858C18.9098 2.64254 22.1789 3.99885 24.59 6.40997C27.0011 8.82109 28.3575 12.0901 28.3614 15.5C28.3585 18.7338 27.1314 21.8466 24.9269 24.2125C23.4716 22.4897 21.5766 21.1935 19.4435 20.4615C20.1841 19.8609 20.7814 19.1028 21.1921 18.2423C21.6027 17.3818 21.8163 16.4406 21.8174 15.4871C21.8148 14.644 21.646 13.8097 21.3205 13.0319C20.995 12.2541 20.5192 11.5482 19.9205 10.9546C19.3217 10.361 18.6117 9.8914 17.8311 9.57265C17.0506 9.2539 16.2148 9.09229 15.3717 9.09708C14.5289 9.0996 13.6949 9.2683 12.9174 9.59354C12.1399 9.91877 11.4342 10.3941 10.8407 10.9925C10.2471 11.5908 9.77743 12.3003 9.45842 13.0803C9.13942 13.8604 8.97739 14.6957 8.98161 15.5385C8.98161 17.5252 9.91403 19.281 11.3426 20.4529C9.25288 21.1651 7.38902 22.418 5.94055 24.0842C3.81059 21.733 2.63332 18.6725 2.63858 15.5C2.64254 12.0901 3.99885 8.82109 6.40998 6.40997C8.8211 3.99885 12.0902 2.64254 15.5 2.63858ZM15.4252 11.2357C17.7712 11.2357 19.6766 13.1433 19.6766 15.5385C19.6739 16.6401 19.2436 17.6977 18.4765 18.4884C17.7094 19.2791 16.6652 19.7411 15.5642 19.7771C15.5021 19.7771 15.4423 19.7643 15.3802 19.7643C15.3268 19.7643 15.2754 19.775 15.222 19.7771C14.1157 19.7338 13.0697 19.262 12.305 18.4614C11.5403 17.6608 11.117 16.5942 11.1245 15.4871C11.1245 13.1433 13.0299 11.2357 15.4252 11.2357ZM15.2198 21.9114C15.2712 21.9136 15.3225 21.9285 15.3738 21.9285H15.4252C15.4722 21.9285 15.5107 21.9136 15.5577 21.9114C17.0497 21.9375 18.5183 22.2858 19.8631 22.9325C21.2078 23.5792 22.3968 24.509 23.3486 25.6582C21.1071 27.4094 18.3445 28.3609 15.5 28.3614C12.5911 28.3673 9.76751 27.3788 7.49743 25.5598C8.45168 24.4381 9.63394 23.5326 10.9655 22.9035C12.2971 22.2744 13.7474 21.9362 15.2198 21.9114Z" fill="#464646" />
                  </svg>
                  <p>
                    Bonjour {user.familyName} {user.name}
                  </p>
                </div>
                <div className={`${options ? 'col' : 'hidden'} gap-[2vw] z-20 justify-center items-start py-[2vw] pl-[3vw] pr-[4vw] absolute top-[2.45vw] bg-newBlack rounded-lg`}>
                  <Link to="/compte" className='text-white' onClick={() => {
                    window.scrollTo({
                      top: 0,
                      left: 0,
                      behavior: 'smooth'
                    })
                  }}>
                    Mon espace personnel
                  </Link>
                  <Link className="text-white" onClick={() => {
                    window.localStorage.removeItem('loggedInhtUser')
                    setUser(null)
                  }}>
                    Dconnexion
                  </Link>
                </div>
              </div>
            </div>
        }
        <nav className={`row w-full border-b-2 border-newBlack items-center ${admin ? 'justify-center' : 'justify-between'} px-[7.03125vw] py-[0.78125vw] bg-white`}>
          {
            admin ?
              <>
                <ul className="row text-grey font-bold gap-[3.75vw] navLink" >
                  <Link to="/admin" onClick={() => {
                    window.scrollTo({
                      top: 0,
                      left: 0,
                      behavior: 'smooth'
                    })
                  }}>
                    <h3>
                      Administration
                    </h3>
                  </Link>
                </ul>
              </>
              :
              <>
                <Link to="/" className="logo" onClick={() => {
                  window.scrollTo({
                    top: 0,
                    left: 0,
                    behavior: 'smooth'
                  })
                }}>
                  INHT
                </Link>
                <ul className="row text-grey font-bold gap-[3.75vw] navLink" >
                  <Link to="/inht" onClick={() => {
                    window.scrollTo({
                      top: 0,
                      left: 0,
                      behavior: 'smooth'
                    })
                  }}>
                    INSTITUT
                  </Link>
                  <Link to="/formations" onClick={() => {
                    window.scrollTo({
                      top: 0,
                      left: 0,
                      behavior: 'smooth'
                    })
                  }}>FORMATIONS</Link>
                  <Link to="/temoignages" onClick={() => {
                    window.scrollTo({
                      top: 0,
                      left: 0,
                      behavior: 'smooth'
                    })
                  }}>
                    TEMOIGNAGES
                  </Link>
                  <Link to="/contact" onClick={() => {
                    window.scrollTo({
                      top: 0,
                      left: 0,
                      behavior: 'smooth'
                    })
                  }}>CONTACT</Link>
                  <Link to="/compte" onClick={() => {
                    window.scrollTo({
                      top: 0,
                      left: 0,
                      behavior: 'smooth'
                    })
                  }}>MON COMPTE
                  </Link>
                  <Link to="/admin" onClick={() => {
                    window.scrollTo({
                      top: 0,
                      left: 0,
                      behavior: 'smooth'
                    })
                  }}>
                    ADMINSTRATION
                  </Link>
                </ul>
              </>
          }

        </nav>
      </div>

      <Routes>
        <Route path="/" element={<Home />} />
        <Route path="/admin" element={<Administration user={user} setUser={setUser} />} />
        <Route path="/admin/:id" element={<View />} />
        <Route path="/inht" element={<Home />} />
        <Route path="/formations" element={<Formations />} />
        <Route path="/formations/bt" element={<Formation formation={'bt'} />} />
        <Route path="/formations/bts" element={<Formation formation={'bts'} />} />
        <Route path="/temoignages" element={<Temoignages />} />
        <Route path="/contact" element={<Contact />} />
        <Route path="/compte" element={<Compte user={user} setUser={setUser} />} />
        <Route path="/compte/candidater" element={user ? < Candidature user={user} setUser={setUser} /> : <Navigate replace to="/compte" />} />
      </Routes>

    </div>
  );
}

export default App;