import { useLocation, useNavigate } from 'react-router-dom'
import candidatureService from '../services/candidature';


const View = () => {
    const navigate = useNavigate()
    const location = useLocation()
    const { userCandidate } = location.state

    const handleAdmission = (status) => {
        candidatureService.update(userCandidate.candidatures[userCandidate.candidatures.length - 1].id, { status: status })
        navigate('/admin', { replace: true })
    }

    let specialty = ''

    if (userCandidate.candidatures[userCandidate.candidatures.length - 1].specialty === 'rec') {
        specialty = 'Rception'
    }
    else if (userCandidate.candidatures[userCandidate.candidatures.length - 1].specialty === 'aht') {
        specialty = 'Administration htelire Tourisme'
    }
    else if (userCandidate.candidatures[userCandidate.candidatures.length - 1].specialty === 'avc') {
        specialty = 'Agent de voyage Cuisine'
    }
    else if (userCandidate.candidatures[userCandidate.candidatures.length - 1].specialty === 'pr') {
        specialty = 'Ptisserie Restaurant'
    }

    return (
        <div className="col items-center p-[8vw] justify-center w-full gap-14">
            <h2 className='homeText text-newBlack'>Candidature de {userCandidate.name} {userCandidate.familyName}</h2>
            <div className='col self-start gap-4'>
                <div className='row items-center gap-3'>
                    <p className='description text-newBlack font-bold'>
                        Nom:
                    </p>
                    <p className='description'>
                        {userCandidate.familyName}
                    </p>
                </div>
                <div className='row items-center gap-3'>
                    <p className='description text-newBlack font-bold'>
                        Prnom:
                    </p>
                    <p className='description'>
                        {userCandidate.name}
                    </p>
                </div>
                <div className='row items-center gap-3'>
                    <p className='description text-newBlack font-bold'>
                        Date de naissance:
                    </p>
                    <p className='description'>
                        {userCandidate.candidatures[userCandidate.candidatures.length - 1].birthDate}
                    </p>
                </div>
                <div className='row items-center gap-3'>
                    <p className='description text-newBlack font-bold'>
                        Profil:
                    </p>
                    <p className='description'>
                        {userCandidate.candidatures[userCandidate.candidatures.length - 1].ts ? 'Technicien suprieur' : 'Technicien'}
                    </p>
                </div>
                <div className='row items-center gap-3'>
                    <p className='description text-newBlack font-bold'>
                        Filire souhaite:
                    </p>
                    <p className='description'>
                        {specialty}
                    </p>
                </div>
                <div className='row items-center gap-3'>
                    <p className='description text-newBlack font-bold'>
                        Wilaya:
                    </p>
                    <p className='description'>
                        {userCandidate.candidatures[userCandidate.candidatures.length - 1].wilaya}
                    </p>
                </div>
                <div className='row items-center gap-3'>
                    <p className='description text-newBlack font-bold'>
                        Adresse:
                    </p>
                    <p className='description'>
                        {userCandidate.candidatures[userCandidate.candidatures.length - 1].address}
                    </p>
                </div>
            </div>
            <div className='row w-full items-center justify-center gap-10 '>
                <button className='btn' onClick={handleAdmission(true)}>
                    Admettre l'tudiant
                </button>
                <button className='btn bg-red' onClick={handleAdmission(false)}>
                    Refuser l'tudiant
                </button>
            </div>


        </div>
    )
}

export default View