import contact from '../assets/contact.jpg';
import candidatureService from '../services/candidature';
import { useState } from 'react';
import { useNavigate } from 'react-router-dom';

import ImageKit from "imagekit-javascript"

const Candidature = ({ user, setUser }) => {
    const navigate = useNavigate()
    const [level, setLevel] = useState('bt');
    const [bac, setBac] = useState(false);
    const [birthDate, setBirthDate] = useState('');
    const [wilaya, setWilaya] = useState('Adrar');
    const [address, setAddress] = useState('');
    const [specialty, setSpecialty] = useState('rec');

    const [candidature, setCandidature] = useState({
        state: true,
        error: ''
    })

    const handleChange = (e) => {
        setLevel(e.target.value);
        if (e.target.value === 'bt') {
            setBac(false);
        }
    };

    const handleBac = (e) => {
        setBac(!bac);
    };

    const arrayNouns = [
        'bulletin',
        'bac',
        'certificat',
        'naissance',
        'phtisiologie',
        'general',
        'photo',
    ]


    const [selectedImage, setSelectedImage] = useState([
        null,
        null,
        null,
        null,
        null,
        null,
        null,
    ]);

    let imagekit = new ImageKit({
        publicKey: process.env.REACT_APP_IMAGE_KIT_PUBLIC_API_KEY,
        privateKey: process.env.REACT_APP_IMAGE_KIT_PRIVATE_KEY,
        urlEndpoint: process.env.REACT_APP_IMAGE_KIT_URL_ENDPOINT,
    });


    const handleCandidature = async (e) => {
        e.preventDefault();

        try {

            if (
                ((level === 'bt') && (!birthDate || !address || !selectedImage[2] || !selectedImage[3] || !selectedImage[4] || !selectedImage[5] || !selectedImage[6]))
                ||
                ((level === 'bts') && (bac === false) && (!birthDate || !address || !selectedImage[1] || !selectedImage[2] || !selectedImage[3] || !selectedImage[4] || !selectedImage[5] || !selectedImage[6]))
                ||
                ((level === 'bts') && (bac === true) && (!birthDate || !address || !selectedImage[0] || !selectedImage[2] || !selectedImage[3] || !selectedImage[4] || !selectedImage[5] || !selectedImage[6]))
            ) {
                throw 1
            }

            const candidature = {
                ts: level === 'bt' ? false : true,
                birthDate: birthDate,
                wilaya: wilaya,
                address: address,
                specialty: specialty
            }

            let secret;

            const loggedUser = await candidatureService.candidater(candidature);
            setUser(loggedUser.userWithoutPass)
            window.localStorage.setItem(
                'loggedInhtUser', JSON.stringify(loggedUser)
            )

            let i = 0;

            for await (const image of selectedImage) {
                secret = await candidatureService.upload();
                if (image) {
                    imagekit.upload({
                        file: image,
                        fileName: `${arrayNouns[i]}.png`,
                        folder: `INHT/Users/${user.id}`,
                        token: secret.token,
                        signature: secret.signature,
                        expire: secret.expire
                    }, function (err, result) {
                        console.log(result);
                    })
                }
                i++;
            }

            navigate('/compte', { replace: true })

        } catch (exception) {
            if (exception === 1) {
                setCandidature({
                    state: false,
                    error: 'Veuillez remplir tous les champs et joindre tous les documents ncessaires'
                })
            }

            setTimeout(() => {
                setCandidature({
                    state: true,
                    error: ''
                })
            }, 5000)
        }
    }

    const year = new Date().getFullYear();

    return (
        <div>
            <header className='w-full h-[29.6875vw] bg-cover bg-center relative flex items-center justify-center pt-[5vw]' style={{
                backgroundImage: `url("${contact}") `
            }} >
                <h1>
                    CANDIDATURE
                </h1>
            </header>
            <div id="container" className='px-[6.25vw] pt-[6.25vw] col items-start gap-[3.125vw]'>
                <div id="Candidature" className='w-full col gap-[2.1875vw] items-start'>
                    <div className='row justify-between w-full items-center'>
                        <p className='semiTitle font-medium'>
                            Spcifiez votre niveau scolaire
                        </p>
                        <select id="niveau" name="niveau" value={level} onChange={handleChange} className='border-newBlack bg-white border-2 p-[0.78125vw] w-[17.96875vw] h-[3.28125vw]'>
                            <option value="bt">2eme Anne Secondaire</option>
                            <option value="bts">3eme Anne Secondaire</option>
                        </select>
                    </div>

                    <form encType="multipart/form-data" onSubmit={handleCandidature} className='col gap-[1.71875vw] w-full'>
                        <div className='row items-center gap-[1.71875vw] w-full'>
                            <div className={` ${level === 'bts' ? 'row' : 'hidden'} w-full gap-[1.5vw]`}>
                                <p className='text-newBlack '>Titulaire du BAC {year} ?</p>
                                <input type="checkbox" id="bac" name="bac" value={bac} onChange={handleBac} className="w-[18px] h-[18px] pt-3 text-blue-600 bg-gray-100 border-gray-300 rounded focus:ring-blue-500 dark:focus:ring-blue-600 dark:ring-offset-gray-800 focus:ring-2 dark:bg-gray-700 dark:border-gray-600" />
                            </div>
                            <div className={` ${level === 'bt' ? 'hidden' : ''} col w-full gap-[0.625vw]`}>
                                <p className='text-newBlack '> {bac ? 'Copie du BAC' : 'Bulletin du 3me trimestre avec moyenne suprieure ou gal  08/20'} <span className=' text-red  '>*</span></p>
                                <input
                                    type="file"
                                    accept="image/*"
                                    style={{ display: 'none' }}
                                    id="bacOuBulletin-file"
                                    onChange={({ target }) => {
                                        let swap = [...selectedImage];
                                        if (bac) {
                                            swap[0] = target.files[0];
                                            setSelectedImage([...swap])
                                        } else {
                                            swap[1] = target.files[0];
                                            setSelectedImage([...swap])
                                        }
                                    }
                                    }
                                />
                                <label htmlFor="bacOuBulletin-file" className='self-start btn'>
                                    {bac ? selectedImage[0] ? selectedImage[0].name : 'Choisir l\'image' : selectedImage[1] ? selectedImage[1].name : 'Choisir l\'image'}
                                </label>
                            </div>
                        </div>
                        <div className='row items-center justify-between w-full'>
                            <div className={`gap-[1.5vw]`}>
                                <p className='text-newBlack '>Certificat de scolarit <span className=' text-red  '>*</span></p>
                            </div>
                            <input
                                type="file"
                                accept="image/*"
                                style={{ display: 'none' }}
                                id="certificat-file"
                                onChange={({ target }) => {
                                    let swap = [...selectedImage];
                                    swap[2] = target.files[0];
                                    setSelectedImage([...swap])
                                }
                                }
                            />
                            <label htmlFor="certificat-file" className=' btn'>
                                {selectedImage[2] ? selectedImage[2].name : 'Choisir l\'image'}
                            </label>
                        </div>
                        <div className='row items-center justify-between w-full'>
                            <div className={`gap-[1.5vw]`}>
                                <p className='text-newBlack '>Extrait de naissance <span className=' text-red  '>*</span></p>
                            </div>
                            <input
                                type="file"
                                accept="image/*"
                                style={{ display: 'none' }}
                                id="naissance-file"
                                onChange={({ target }) => {
                                    let swap = [...selectedImage];
                                    swap[3] = target.files[0];
                                    setSelectedImage([...swap])
                                }
                                }
                            />
                            <label htmlFor="naissance-file" className=' btn'>
                                {selectedImage[3] ? selectedImage[3].name : 'Choisir l\'image'}
                            </label>
                        </div>
                        <div className='row items-center justify-between w-full'>
                            <div className={`gap-[1.5vw]`}>
                                <p className='text-newBlack '>Certificat mdical Phtisiologie <span className=' text-red  '>*</span></p>
                            </div>
                            <input
                                type="file"
                                accept="image/*"
                                style={{ display: 'none' }}
                                id="phtisiologie-file"
                                onChange={({ target }) => {
                                    let swap = [...selectedImage];
                                    swap[4] = target.files[0];
                                    setSelectedImage([...swap])
                                }}
                            />
                            <label htmlFor="phtisiologie-file" className=' btn'>
                                {selectedImage[4] ? selectedImage[4].name : 'Choisir l\'image'}
                            </label>
                        </div>
                        <div className='row items-center justify-between w-full'>
                            <div className={`gap-[1.5vw]`}>
                                <p className='text-newBlack '>Certificat mdical gnral <span className=' text-red  '>*</span></p>
                            </div>
                            <input
                                type="file"
                                accept="image/*"
                                style={{ display: 'none' }}
                                id="general-file"
                                onChange={({ target }) => {
                                    let swap = [...selectedImage];
                                    swap[5] = target.files[0];
                                    setSelectedImage([...swap])
                                }}
                            />
                            <label htmlFor="general-file" className=' btn'  >
                                {selectedImage[5] ? selectedImage[5].name : 'Choisir l\'image'}
                            </label>
                        </div>
                        <div className='row items-center justify-between w-full'>
                            <div className={`gap-[1.5vw]`}>
                                <p className='text-newBlack '>Photo <span className=' text-red  '>*</span></p>
                            </div>
                            <input
                                type="file"
                                accept="image/*"
                                style={{ display: 'none' }}
                                id="photo-file"
                                onChange={({ target }) => {
                                    let swap = [...selectedImage];
                                    swap[6] = target.files[0];
                                    setSelectedImage([...swap])
                                }}
                            />
                            <label htmlFor="photo-file" className='btn'>
                                {selectedImage[6] ? selectedImage[6].name : 'Choisir l\'image'}
                            </label>
                        </div>
                        <div className='row gap-[1.71875vw] w-full'>
                            <div className='col gap-[0.625vw]'>
                                <p className='text-newBlack '>Date de naissance <span className=' text-red  '>*</span></p>
                                <input value={birthDate} type="date" id="birthday" name="birthday" className='border-newBlack border-2 p-[0.78125vw] w-[20vw]' min={`${year - 25}-01-01`} max={`${year - 18}-12-31`} onChange={({ target }) => {
                                    setBirthDate(target.value)
                                }}></input>
                            </div>
                            <div className='col gap-[0.625vw]'>
                                <p className='text-newBlack '>Wilaya <span className=' text-red'>*</span></p>
                                <select value={wilaya} id="wilaya" name="wilaya" className='border-newBlack bg-white border-2 p-[0.78125vw] w-[20vw] h-[3.28125vw]' onChange={({ target }) => {
                                    setWilaya(target.value)
                                }}>
                                    <option value="Adrar">1- Adrar</option>
                                    <option value="Chlef">2- Chlef</option>
                                    <option value="Laghouat">3- Laghouat</option>
                                    <option value="Oum El Bouaghi">4- Oum El Bouaghi</option>
                                    <option value="Batna">5- Batna</option>
                                    <option value="Bjaa">6- Bjaa</option>
                                    <option value="Biskra">7- Biskra</option>
                                    <option value="Bchar">8- Bchar</option>
                                    <option value="Blida">9- Blida</option>
                                    <option value="Bouira">10- Bouira</option>
                                    <option value="Tamanrasset">11- Tamanrasset</option>
                                    <option value="Tbessa">12- Tbessa</option>
                                    <option value="Tlemcen">13- Tlemcen</option>
                                    <option value="Tiaret">14- Tiaret</option>
                                    <option value="Tizi Ouzou">15- Tizi Ouzou</option>
                                    <option value="Alger">16- Alger</option>
                                    <option value="Djelfa">17- Djelfa</option>
                                    <option value="Jijel">18- Jijel</option>
                                    <option value="Stif">19- Stif</option>
                                    <option value="Sada">20- Sada</option>
                                    <option value="Skikda">21- Skikda</option>
                                    <option value="Sidi Bel Abbs">22- Sidi Bel Abbs</option>
                                    <option value="Annaba">23- Annaba</option>
                                    <option value="Guelma">24- Guelma</option>
                                    <option value="Constantine">25- Constantine</option>
                                    <option value="Mda">26- Mda</option>
                                    <option value="Mostaganem">27- Mostaganem</option>
                                    <option value="M'Sila">28- M'Sila</option>
                                    <option value="Mascara">29- Mascara</option>
                                    <option value="Ouargla">30- Ouargla</option>
                                    <option value="Oran">31- Oran</option>
                                    <option value="El Bayadh">32- El Bayadh</option>
                                    <option value="Illizi">33- Illizi</option>
                                    <option value="Bordj Bou Arreridj">34- Bordj Bou Arreridj</option>
                                    <option value="Boumerds">35- Boumerds</option>
                                    <option value="El Tarf">36- El Tarf</option>
                                    <option value="Tindouf">37- Tindouf</option>
                                    <option value="Tissemsilt">38- Tissemsilt</option>
                                    <option value="El Oued">39- El Oued</option>
                                    <option value="Khenchela">40- Khenchela</option>
                                    <option value="Souk Ahras">41- Souk Ahras</option>
                                    <option value="Tipaza">42- Tipaza</option>
                                    <option value="Mila">43- Mila</option>
                                    <option value="An Defla">44- An Defla</option>
                                    <option value="Nama">45- Nama</option>
                                    <option value="An Tmouchent">46- An Tmouchent</option>
                                    <option value="Ghardaa">47- Ghardaa</option>
                                    <option value="Relizane">48- Relizane</option>
                                    <option value="Timimoun">49- Timimoun</option>
                                    <option value="Bordj Badji Mokhtar">50- Bordj Badji Mokhtar</option>
                                    <option value="Ouled Djellal">51- Ouled Djellal</option>
                                    <option value="Bni Abbs">52- Bni Abbs</option>
                                    <option value="In Salah">53- In Salah</option>
                                    <option value="In Guezzam">54- In Guezzam</option>
                                    <option value="Touggourt">55- Touggourt</option>
                                    <option value="Djanet">56- Djanet</option>
                                    <option value="El M'Ghair">57- El M'Ghair</option>
                                    <option value="El Menia">58- El Menia</option>
                                </select>
                            </div>
                            <div className='col w-full gap-[0.625vw]'>
                                <p className='text-newBlack '>Adresse <span className=' text-red  '>*</span></p>
                                <input value={address} type="text" className='border-newBlack border-2 p-[0.78125vw] w-full' onChange={({ target }) => {
                                    setAddress(target.value)
                                }}>
                                </input>
                            </div>
                        </div>
                        <div className='row justify-between w-full items-center'>
                            <p className='text-newBlack '>Choissisez une filire <span className=' text-red  '>*</span></p>
                            <select value={specialty} id="niveau" name="niveau" className='border-newBlack bg-white border-2 p-[0.78125vw] w-[20vw] h-[3.28125vw]' onChange={({ target }) => {
                                setSpecialty(target.value)
                            }}>
                                <option value="rec">Rception</option>
                                <option value="aht">Administration htelire Tourisme</option>
                                <option value="avc">Agent de voyage Cuisine</option>
                                <option value="pr">Ptisserie Restaurant</option>
                            </select>
                        </div>
                        <p className={`text-red ${candidature.state ? 'hidden' : 'flex'}`}>
                            {candidature.error}
                        </p>
                        <button type="submit" className='btn self-start'>
                            Candidature
                        </button>
                    </form>
                </div>
            </div>
        </div>
    )
}

export default Candidature;