import axios from 'axios'
const baseUrl = '/api/candidature'

let token = null

const setToken = newToken => {
    token = `bearer ${newToken}`
}

const candidater = async candidature => {
    const config = {
        headers: { Authorization: token },
    }

    const response = await axios.post(baseUrl, candidature, config)
    return response.data
}

const update = async (id, newObject) => {
    const config = {
        headers: { Authorization: token },
    }

    const response = await axios.put(`${baseUrl}/${id}`, newObject, config)
    return response.data
}

const upload = async () => {
    const response = await axios.post(`${baseUrl}/upload`)
    return response.data
}

export default { setToken, candidater, update, upload }