import { Link } from 'react-router-dom';
import loginService from '../services/login'
import usersService from '../services/users'
import candidatureService from '../services/candidature'
import { useState } from 'react';

const Compte = ({ user, setUser }) => {
    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')
    const [email1, setEmail1] = useState('')
    const [password1, setPassword1] = useState('')
    const [password2, setPassword2] = useState('')
    const [familyName, setFamilyName] = useState('')
    const [name, setName] = useState('')
    const [phone, setPhone] = useState('')
    const [sexe, setSexe] = useState('Man')
    const [login, setLogin] = useState(true)
    const [signUp, setSignUp] = useState({
        state: true,
        error: ''
    })

    const handleLogin = async (event) => {
        event.preventDefault()
        try {
            const user = await loginService.login({
                email, password,
            });

            setEmail('')
            setPassword('')

            setUser(user.userWithoutPass)
            window.localStorage.setItem(
                'loggedInhtUser', JSON.stringify(user)
            )
            candidatureService.setToken(user.token)
        } catch (exception) {
            setLogin(false);
            setTimeout(() => {
                setLogin(true)
            }, 5000)
        }
    }

    const handleSignUp = async (event) => {
        event.preventDefault()
        try {

            if (!name || !familyName || !email1 || !password1 || !password2 || !phone || !sexe) {
                throw 1
            }

            if (password1 !== password2) {
                throw 2
            }

            const user = {
                name: name,
                familyName: familyName,
                email: email1,
                password: password1,
                phone: phone,
                sexe: sexe
            }

            setEmail('')
            setEmail1('')
            setPassword('')
            setPassword1('')
            setPassword2('')
            setFamilyName('')
            setName('')
            setPhone('')

            await usersService.signUp(user);
            const loggedUser = await loginService.login({
                email: email1, password: password1
            });
            setUser(loggedUser.userWithoutPass)
            window.localStorage.setItem(
                'loggedInhtUser', JSON.stringify(loggedUser)
            )
            candidatureService.setToken(loggedUser.token)

        } catch (exception) {
            if (exception === 1) {
                setSignUp({
                    state: false,
                    error: 'Veuillez remplir tous les champs'
                })
            } else if (exception === 2) {
                setSignUp({
                    state: false,
                    error: 'les mots de passe ne sont pas identiques'
                })
            }
            else {
                setSignUp({
                    state: false,
                    error: 'adresse email dj utilise'
                })
            }
            setTimeout(() => {
                setSignUp({
                    state: true,
                    error: ''
                })
            }, 5000)
        }
    }

    if (user === null) {
        return (
            <div>
                <header className='w-full h-[29.6875vw] relative flex items-center justify-center pt-[5vw] '  >
                    <p className='semiTitle text-center font-bold'>
                        Pour toute demande de brochure, participation  une runion dinformation ou
                        <br />
                        inscription au concours, merci de bien vouloir crer votre compte en quelques clics.
                        <br />
                        Vous avez dj un compte ? Identifiez-vous
                    </p>
                </header>
                <div id="container" className='px-[6.25vw] pt-[6.25vw] col items-start gap-[3.125vw]'>
                    <div id="Sign In" className='w-full col gap-[2.1875vw] items-start'>
                        <p className='semiTitle font-medium'>
                            Dj un compte ? Identifiez-vous
                        </p>
                        <form onSubmit={handleLogin} className='col gap-[1.71875vw] w-full'>
                            <div className='row gap-[1.71875vw] w-full'>
                                <div className='col w-full gap-[0.625vw]'>
                                    <p className='text-newBlack '>Adresse email</p>
                                    <input value={email} name="Email" type="text" className='border-newBlack border-2 p-[0.78125vw] w-full' onChange={({ target }) => setEmail(target.value)}>
                                    </input>
                                </div>
                                <div className='col w-full gap-[0.625vw]'>
                                    <p className='text-newBlack '>Mot de passe</p>
                                    <input value={password} name="Password" type="password" className='border-newBlack border-2 p-[0.78125vw] w-full' onChange={({ target }) => setPassword(target.value)}>
                                    </input>
                                </div>
                            </div>
                            <p className={`text-red ${login ? 'hidden' : 'flex'}`}>
                                Vos identifiants sont errons.
                            </p>
                            <button type="submit" className='btn self-start'>
                                Connexion
                            </button>
                        </form>
                    </div>
                    <div id="Sign Up" className='w-full col gap-[2.1875vw] items-start'>
                        <p className='semiTitle font-medium'>
                            Pas de compte ? Inscrivez-vous
                        </p>
                        <form onSubmit={handleSignUp} className='col gap-[1.71875vw] w-full'>
                            <div className='row gap-[1.71875vw] w-full'>
                                <div className='col gap-[0.625vw]'>
                                    <p className='text-newBlack '>Civilit <span className=' text-red  '>*</span></p>
                                    <select id="sexe" name="sexe" value={sexe} onChange={({ target }) => { setSexe(target.value) }} className='border-newBlack bg-white border-2 p-[0.78125vw] w-[17.96875vw] h-[3.28125vw]'>
                                        <option value="Man">M.</option>
                                        <option value="Woman">Mme</option>
                                    </select>
                                </div>
                                <div className='col w-full gap-[0.625vw]'>
                                    <p className='text-newBlack ' >Prnom <span className=' text-red  '>*</span></p>
                                    <input type="text" className='border-newBlack border-2 p-[0.78125vw] w-full' value={name} onChange={({ target }) => setName(target.value)}>
                                    </input>
                                </div>
                                <div className='col w-full gap-[0.625vw]'>
                                    <p className='text-newBlack ' >Nom <span className=' text-red  '>*</span></p>
                                    <input type="text" className='border-newBlack border-2 p-[0.78125vw] w-full' value={familyName} onChange={({ target }) => setFamilyName(target.value)}>
                                    </input>
                                </div>
                            </div>
                            <div className='row gap-[1.71875vw] w-full'>
                                <div className='col w-full gap-[0.625vw]'>
                                    <p className='text-newBlack ' >Adresse Email <span className=' text-red  '>*</span></p>
                                    <input value={email1} type="text" className='border-newBlack border-2 p-[0.78125vw] w-full' onChange={({ target }) => setEmail1(target.value)}>
                                    </input>
                                </div>
                                <div className='col w-full gap-[0.625vw]'>
                                    <p className='text-newBlack ' >Tlphone <span className=' text-red  '>*</span></p>
                                    <input value={phone} type="text" className='border-newBlack border-2 p-[0.78125vw] w-full' onChange={({ target }) => setPhone(target.value)}>
                                    </input>
                                </div>
                            </div>
                            <div className='row gap-[1.71875vw] w-full'>
                                <div className='col w-full gap-[0.625vw]'>
                                    <p className='text-newBlack '>Mot de passe <span className=' text-red  '>*</span></p>
                                    <input type="password" className='border-newBlack border-2 p-[0.78125vw] w-full' value={password1} onChange={({ target }) => setPassword1(target.value)}>
                                    </input>
                                </div>
                                <div className='col w-full gap-[0.625vw]'>
                                    <p className='text-newBlack ' >Confirmer Mot de passe <span className=' text-red  '>*</span></p>
                                    <input type="password" className='border-newBlack border-2 p-[0.78125vw] w-full' value={password2} onChange={({ target }) => setPassword2(target.value)}>
                                    </input>
                                </div>
                            </div>
                            <p className={`text-red ${signUp.state ? 'hidden' : 'flex'}`}>
                                {signUp.error}
                            </p>
                            <button type="submit" className='btn self-start'>
                                Inscription
                            </button>
                        </form>
                    </div>
                </div>
            </div>
        )
    } else {
        return (
            <div>
                <header className='w-full h-[48vw] relative col gap-[1.5625vw] items-center justify-center pt-[5vw] '  >
                    <p className='semiTitle shadow-[0px_0.88889px_6.22222px_0px_rgba(0,0,0,0.25)] p-[2vw] rounded-lg font-bold'>
                        Bienvenu  votre espace personnel {user.familyName} {user.name}
                        <br />
                        Depuis cet espace vous pourriez candidater  une formation  l'INHT
                    </p>
                    {
                        user.candidatures.length > 0 ?
                            new Date(user.candidatures[user.candidatures.length - 1].createdAt).getFullYear() === new Date().getFullYear() && user.candidatures[user.candidatures.length - 1].status === null ?
                                <p className='text-center'>
                                    Vous avez dj candidat pour cette anne
                                    <br />
                                    Veuillez attendre la rponse de l'administration
                                </p> :
                                new Date(user.candidatures[user.candidatures.length - 1].createdAt).getFullYear() && user.candidatures[user.candidatures.length - 1].status ?
                                    <p className='text-center text-green '>
                                        Fliitation vous avez t admis
                                        <br />
                                        Veuillez nous contacter pour finaliser votre inscription
                                    </p> :
                                    new Date(user.candidatures[user.candidatures.length - 1].createdAt).getFullYear() && user.candidatures[user.candidatures.length - 1].status === false ?
                                        <p className='text-center text-red '>
                                            Dsol vous n'avez pas t admis
                                            <br />
                                            Les places pdagogiques ont t atteintes
                                        </p>
                                        :
                                        <></>
                            :
                            <button className='btn'>
                                <Link to="/compte/candidater" onClick={() => {
                                    window.scrollTo({
                                        top: 0,
                                        left: 0,
                                        behavior: 'smooth'
                                    })
                                }}>
                                    Candidater
                                </Link>
                            </button>
                    }
                </header>
            </div>
        )
    }
}

export default Compte;