import { Link } from 'react-router-dom';
import loginService from '../services/login'
import usersService from '../services/users'
import candidatureService from '../services/candidature';
import { useState, useEffect } from 'react';

const Administration = ({ user, setUser }) => {
    const [users, setUsers] = useState([])

    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')

    const [login, setLogin] = useState(true)

    const handleLogin = async (event) => {
        event.preventDefault()
        try {

            const user = await loginService.login({
                email, password,
            });

            if (user.userWithoutPass.email !== 'admin@inht.app')
                throw new Error('wrong credentials')

            setEmail('')
            setPassword('')

            setUser(user.userWithoutPass)
            usersService.setToken(user.token)
            candidatureService.setToken(user.token)

            const users = await usersService.getUsers()
            setUsers(users)

        } catch (exception) {
            console.log('wrong credentials')
            setLogin(false);
            setTimeout(() => {
                setLogin(true)
            }, 5000)
        }
    }

    useEffect(() => {
        const fetchUsers = async () => {
            const users = await usersService.getUsers()
            setUsers(users)
        }
        if (user && user.email !== 'admin@inht.app') {
            window.localStorage.removeItem('loggedInhtUser')
            setUser(null)
        } else if (user && user.email === 'admin@inht.app') {
            fetchUsers()
        }
    }, [])

    return (

        <div className={`col items-center justify-center gap-[5vw] w-full ${user ? 'h-auto p-[10vw]' : 'h-[100vh] p-0'}`}>
            {
                user
                    ?
                    <table className='w-[70vw] border-b rounded-md bg-newBlack border-newBlack my-8 border-collapse table-auto'>
                        <thead >
                            <tr className='py-4 text-white'>
                                <th>
                                    Prnom
                                </th>
                                <th>
                                    Nom
                                </th>
                                <th>
                                    Date de naissance
                                </th>
                                <th>
                                    wilaya
                                </th>
                                <th>
                                    Numro de tlphone
                                </th>
                                <th>
                                    Candidature
                                </th>
                            </tr>
                        </thead>
                        <tbody className='bg-white dark:bg-slate-800 text-center'>
                            {
                                users.map(userFound => {
                                    return (
                                        <tr key={userFound.id} className='border-b py-1'>
                                            <td>
                                                {userFound.name}
                                            </td>
                                            <td>
                                                {userFound.familyName}
                                            </td>
                                            <td>
                                                {userFound.candidatures[userFound.candidatures.length - 1].birthDate}
                                            </td>
                                            <td>
                                                {userFound.candidatures[userFound.candidatures.length - 1].wilaya}
                                            </td>
                                            <td>
                                                {userFound.phone}
                                            </td>
                                            <td>
                                                <Link to={`/admin/${userFound.id}`} state={{ userCandidate: userFound }}>
                                                    <button className='p-2 px-4 bg-niceBlue text-white rounded-md'>
                                                        Voir
                                                    </button>
                                                </Link>
                                            </td>
                                        </tr>
                                    )
                                })
                            }
                        </tbody>
                    </table>
                    : <form id="Sign In" onSubmit={handleLogin} className='col items-center gap-[1.71875vw] w-full'>
                        <div className='col items-center gap-[1.71875vw] w-full'>
                            <input value={email} name="Email" type="text" className='border-newBlack border-2 p-[0.78125vw] w-[20vw]' placeholder='Adresse Email' onChange={({ target }) => setEmail(target.value)}>
                            </input>
                            <input value={password} name="Password" type="password" className='border-newBlack border-2 p-[0.78125vw] w-[20vw]' placeholder='Mot de passe' onChange={({ target }) => setPassword(target.value)}>
                            </input>
                        </div>
                        <p className={`text-red ${login ? 'hidden' : 'flex'}`}>
                            Vos identifiants sont errons.
                        </p>
                        <button type="submit" className='btn'>
                            Connexion
                        </button>
                    </form>
            }
        </div>
    )
}

export default Administration